rer les applications sur le nouveau téléphone, cela nécessitait de réaliser la première et la deuxième étape. Pour cette première étape, il fallait mettre à jour les dépendances (aussi appelés librairies dans d’autres langages de programmation) et les plugins d’android tout en faisant attention à ce que toutes les dépendances soient compatibles à une version android, l’explication étant que chaque version d’une dépendance ne supporte que certaines versions d’android. ")]),_c('p',[_vm._v(" Dues aux nouvelles dépendances, la deuxième étape était de corriger toutes les erreurs de compilation présentes sur les applications. Comme exemple, sur l’ensemble 1, on peut voir qu’on doit rajouter une variable parent pour chaque composant lié au layout (= élément servant de vue à l’application) dans les nouvelles versions d’android. ")])])],1),_c('v-container',{staticClass:"spaceBtw align"},[_c('v-card-text',{staticClass:"dirCol"},[_c('img',{staticClass:"img3 borderImg",attrs:{"alt":"Image not found","src":require("@/assets/code_accelero_S8.png")}}),_c('img',{staticClass:"img3 borderImg",attrs:{"alt":"Image not found","src":require("@/assets/code_accelero_S20.png")}}),_c('p',{staticClass:"legImg"},[_vm._v("Ensemble 2 : Comparaison de deux versions du code de ElectroMob entre S8 et S20")])]),_c('v-card-text',{staticClass:"dirCol"},[_c('img',{staticClass:"img0 borderImg",attrs:{"alt":"Image not found","src":require("@/assets/comparaison_graph_accelero.png")}}),_c('p',{staticClass:"legImg"},[_vm._v("Figure 2 : Comparaison de graphiques des axes d'accélération entre S8 et S20")])])],1),_c('v-container',[_c('v-card-text',{staticClass:"txt"},[_c('p',[_vm._v(" La troisième étape était la complexe et la plus importante, le but étant de définir si les capteurs enregistraient les bonnes données et si ces données étaient cohérentes avec celles enregistrées par le téléphone Samsung S8 (qu’on nommera par la suite S8). En somme, cela demande des compétences d’analyse et de traitement de donné